.card{
    border: 2px solid #d1e0e0;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    box-shadow: 10px 10px 5px -5px rgb(102, 153, 153);
    border-radius: 6px;
}
.character__image{
    width: 175px;
    height: 250px;
}
.character__name{
    font-size: 19px;
    margin: 10px 0;
}
.character__home{
    width: 100px;
    height: 100px;
    margin: 0;
}
